.list-of-grid {

    a {
        text-decoration: none;
    }

    .grid__item {
        margin-bottom: 30px;
        padding: 10px;
        transition: background-color .3s ease-in-out;
        -webkit-transition: background-color .3s ease-in-out;
        -moz-transition: background-color .3s ease-in-out;
        border-radius: 5px;

        &:hover {
            background-color: #fff;
            box-shadow: 0px 3px 20px #e4e4e4;

            .item__image {
                &::after {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }

        .item__image {
            position: relative;

            img {
                max-width: 100%;
                height: auto;
                width: 100%;
            }

            &::after {
                background-color: rgba(21, 92, 99, 0.5);
                transition: opacity 0.2s ease-in-out;
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                opacity: 0;
                visibility: hidden;
            }
        }

        .item__content {
            text-align: center;

            .item__title {
                color: #155c63;
                font-size: 1rem;
                margin-top: 20px;
                margin-bottom: 10px;
            }

            .item__description {
                color: #155c63;
                line-height: 2;
            }
        }
    }
}